/**
 * from:https://github.com/hujiulong/simple-react/blob/chapter-1/src/index.js
 */
const React = {
    createElement,
    render: (vnode, container) => {
        // ‰∏∫‰∫ÜÊØèÊ¨°ÊâßË°åÊó∂Ê∏ÖÁ©∫
        container.innerHTML = '';
        return render(vnode, container);
    },
    Component
};

/**
 * React.createElement()
 * transform-react-jsx ÊòØ jsx ËΩ¨Êç¢Êàê js ÁöÑbabel Êèí‰ª∂
 * ‰ªñÊúâ‰∏Ä‰∏™ pramgma È°πÔºåÊù•ÂÆö‰πâËΩ¨Êç¢ jsx ÁöÑÊñπÊ≥ïÂêçÁß∞
 * ËøôÈáåÊàëÂÆö‰πâËøô‰∏™ÂáΩÊï∞‰∏∫ createElement
 */
function createElement(tag, attrs, ...children) {
    return {
        tag,
        attrs,
        children
    }
};

/**
 * React.render()
 * renderÊñπÊ≥ïÁöÑ‰ΩúÁî®ÊòØÂ∞ÜËôöÊãüDOMÊ∏≤ÊüìÊàêÁúüÂÆûÁöÑDOM
 */
function render(vnode, container) {
    // ÂΩìvnode‰∏∫Â≠óÁ¨¶‰∏≤Êó∂ÔºåÊ∏≤ÊüìÁªìÊûúÊòØ‰∏ÄÊÆµÊñáÊú¨
    if (typeof vnode === 'string' || vnode instanceof Date) {
        const textNode = document.createTextNode(vnode);
        return container.appendChild(textNode);
    }
    // ÂàõÂª∫Ê†áÁ≠æ
    const dom = document.createElement(vnode.tag);
    // Â±ûÊÄßÊ∏≤Êüì
    if (vnode.attrs) {
        for (const [name, value] of Object.entries(vnode.attrs)) {
            setAttribute(dom, name, value)
        }
    }
    // ÈÄíÂΩíÂ≠êÂÖÉÁ¥†
    vnode.children && vnode.children.forEach(child => render(child, dom));
    // ÊåÇËΩΩ
    return container.appendChild(dom);
}

/**
 * ÁªôdomÊ∏≤ÊüìÂ±ûÊÄß
 */
function setAttribute(dom, name, value) {
    // className => class
    if (name === 'className') name = 'class'

    // ‰∫ã‰ª∂ onXXX
    if (/on\w+/.test(name)) {
        name = name.toLowerCase();
        dom[name] = value || '';
    }
    // style 
    else if (name === 'style') {
        if (!value || typeof value === 'string') {
            dom.style.cssText = value || '';
        } else if (value && typeof value === 'object') {
            for (let name in value) {
                // ÂèØ‰ª•ÈÄöËøástyle={ width: 20 }ËøôÁßçÂΩ¢ÂºèÊù•ËÆæÁΩÆÊ†∑ÂºèÔºåÂèØ‰ª•ÁúÅÁï•ÊéâÂçï‰Ωçpx
                dom.style[name] = typeof value[name] === 'number' ? value[name] + 'px' : value[name];
            }
        }
    }
    // ÂÖ∂‰ªñ
    else {
        if (name in dom) {
            dom[name] = value || '';
        }

        if (value) {
            dom.setAttribute(name, value);
        } else {
            dom.removeAttribute(name);
        }
    }
}

/**
 * React.Component
 * ÁªÑ‰ª∂ÁöÑ Component Á±ª
 */
class Component {
    constructor(props = {}) {
        this.state = {};
        this.props = props;
    }
}

export default React